.productDetailPage {
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  
  .headerPro {
    margin-top: 10px;
    margin-bottom: 20px;
    display: flex;
    background-color: #eee;
    padding: 15px 8px;
    border-radius: 10px;
}
  
  .back {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #760202;
    font-weight: bold;
    margin-inline: auto;
    margin-left: 0;
    gap: 5px;
  }
  
  /* تفاصيل المنتج */
  .productDetail {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  /* معرض الصور */
  .imageGallery {
    flex: 1 1 300px;
    text-align: center;
  }
  
  .mainImageContainer {
    width: 100%;
    text-align: center;
    margin-bottom: 10px;
  }
  
  .productImage {
    max-width: 100%;
    height: auto;
    border-radius: 10px;
  }
  
  .thumbnails {
    display: flex;
    justify-content: center;
    gap: 10px;
  }
  
  .thumbnail {
    width: 90px;
    height: 70px;
    margin-block: 10px;
    object-fit: cover;
    border: 2px solid transparent;
    border-radius: 5px;
    cursor: pointer;
    transition: border 0.3s;
  }
  
  .thumbnail:hover,
  .thumbnail.active {
    border-color: #760202;
  }
  
  /* معلومات المنتج */
  .infoContainer {
    flex: 1 1 300px;
    text-align: right;
  }
  
  .title {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  
  .priceContainer {
    display: flex;
    gap: 10px;
    margin-bottom: 10px;
    align-items: center;
  }
  
  .price {
    font-size: 1.2rem;
    color: #760202;
  }
  
  .oldPrice {
    font-size: 1.2rem;
    color: gray;
  }
  
  .rating {
    display: flex;
    align-items: center;
    gap: 5px;
    margin-bottom: 10px;

  }
  
  .description {
    font-size: 1rem;
    margin-bottom: 20px;
    text-align: justify;
    padding: 8px;
  }
  
  .detailsSection {
    margin-bottom: 20px;
    text-align: right;
  }
  
  .detailsSection h2 {
    margin-bottom: 10px;
    font-size: 1.2rem;
  }
  
  .detailsSection ul {
    list-style: disc;
    padding-right: 20px;
  }
  
  .detailsSection li {
    margin-bottom: 5px;
    font-size: 1rem;
  }
  
  /* أزرار الإجراءات */
  .actionButtons {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-block: 20px;
  }
  
  .buyNow,
  .addToCart,
  .shareButton {
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    color: #fff;
  }
  
  .buyNow {
    background-color: #760202;
  }
  
  .addToCart {
    background-color: #221f1f;
  }
  
  .shareButton {
    background-color: #0044cc;
  }
  
  /* النافذة المنبثقة للمشاركة */
  .shareModalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .shareModal {
    background: #fff;
    padding: 20px;
    border-radius: 10px;
    width: 90%;
    max-width: 600px;
    position: relative;
    text-align: center;
  }
  
  .closeModal {
    position: absolute;
    top: 10px;
    right: 10px;
    background: transparent;
    border: none;
    cursor: pointer;
  }
  
  .shareModal h2 {
    margin-bottom: 10px;
  }
  
  .shareModal p {
    margin-bottom: 15px;
  }
  
  /* بطاقة المشاركة */
  .shareCard {
    width: 512px;
    height: 512px;
    margin: 0 auto 15px auto;
    border: 2px dashed #760202;
    border-radius: 10px;
    background: #fefefe;
    position: relative;
  }
  
  .shareCardContent {
    width: 100%;
    height: 100%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .shareImage img {
    width: 100%;
    max-height: 200px;
    object-fit: cover;
    border-radius: 5px;
  }
  
  .shareDetails {
    text-align: right;
    padding: 5px;
    font-size: 0.9rem;
  }
  
  .shareDetails .shareTitle {
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .shareDetails .sharePrice,
  .shareDetails .shareLink {
    margin-bottom: 3px;
  }
  
  .qrCode {
    margin-top: 10px;
    display: flex;
    justify-content: center;
  }
  
  /* توقيع في الصورة */
  .siteLogo {
    position: absolute;
    bottom: 10px;
    right: 10px;
    opacity: 0.6;
    width: 50px;
  }
  
  .siteLogo img {
    width: 90%;
    margin-inline: auto;
    display: block;
  }
  
  /* زر تنزيل الصورة */
  .downloadButton {
    background-color: #ff4400;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  /* استجابة التصميم للشاشات الصغيرة */
  @media (max-width: 768px) {
    .productDetail {
      flex-direction: column;
      align-items: center;
    }
    .infoContainer {
      text-align: center;
    }
    .priceContainer,
    .rating {
      justify-content: center;
      align-items: center;
      
    }
    .detailsSection ul {
      padding-right: 0;
      list-style-position: inside;
    }
    .actionButtons {
        margin-block: 15px;
      justify-content: center;
    }
    .shareCard {
      width: 90%;
      height: auto;
    }
  }
  